async function getResources(url) {
    try {
        let response = await fetch(url);

        if (!response.ok) {
            throw new Error(`Co poszo nie tak... bd ${response.status}! Odwie stron i sprbuj jeszcze raz`);
        }

        return await response.json();
    } catch (e) {}
}

async function postData(url, data) {
    try {
        let response = await fetch(url, {
            method: 'POST',
            body: data,
        });

        if (!response.ok) {
            throw new Error(`Co poszo nie tak... bd ${response.message}! Odwie stron i sprbuj jeszcze raz`);
        }

        return await response.json();
    } catch (e) {}
}

export { getResources };
export { postData };
